* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  --active-link-color: #00acdc;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  display: block;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: auto;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--active-link-color);
}

.nav__list {
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: var(--active-link-color);
}

.is-active:after {
  content: "";
  background-color: var(--active-link-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
